import React from 'react'
import '../../styles/dashboard.css'
import withLogging from '../../hocs/withLogging.js'
import Header from '../Header.js'
import ExpoItem2 from './ExpoItem2.js'
import {withRouter} from 'react-router-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

class MyExpositions extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {expositions: [], url: false};
    }

    componentDidMount(){
        this.getExpositions();
    }

    getExpositions = () => {
        requester.get('/api/expositions/user')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    let expos = this.state.expositions;
                    expos = res.expositions;

                    this.setState({
                        expositions: expos
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }

    exposition3D = (url) => {
        this.props.history.push('/exposition/view/' + url);
    }

    deleteExpo = (id) => {
        requester.remove('/api/expositions/' + id).then(res => {
            this.getExpositions();
            observer.fire('notifications', res);
        });
    }
    
    addTop = (id) => {
        requester.post({id}, 'api/expositions/top');
    }

    render() {
        let expositions = this.state.expositions;
        let images = this.state.images;

        return (
            <div>
                <Header transparent={true}></Header>
                
                <div className='expo-content2'>
                    <h1>Всички експозиции</h1>
                    <hr />
                    <ul>
                        {
                            expositions.map((expo, i) => 
                            <ExpoItem2 
                                    src={expo ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + expo.mainImage:''} 
                                    title={expo.title} 
                                    key={i} 
                                    expo3D={() => {this.exposition3D(expo.id)}}
                                    addTop={() => {this.addTop(expo.id)}}
                                    deleteExpo={() => {this.deleteExpo(expo.id)}}
                                    isAdmin={this.props.isAdmin}
                                    isAuthor={this.props.expositions.includes(expo.id)}>
                            </ExpoItem2>)
                        }
                    </ul>
                    <hr />
                </div>
            </div>
        );

    }
}
export default withRouter(withLogging(MyExpositions));