import React from 'react'
import {Redirect} from 'react-router-dom'
import requester from '../../helpers/requester'
import styles from '../../styles/exposition.module.css'

import ThreeContainer from '../ThreeJS/ThreeContainer.js'
import Header from '../Header'



export default class ExpositionView extends React.Component {
    constructor(params){
        super(params);

        this.state = {images: [], details: [], startSlideshow: false, redirect: false,
            dynamicUI: {
                position: 'absolute', 
                display: 'none',
                top: 0, 
                left: 0
            },
            pointer: {
                cursor: 'default'
            }
        };
        
        this.hoverImage = this.hoverImage.bind(this);
        this.hideMenu = this.hideMenu.bind(this);
        this.toggleSlideshow = this.toggleSlideshow.bind(this);
        this.redirectImage = this.redirectImage.bind(this);
    }
    componentDidMount(){
        requester.get('/api/expositions/details/' + this.props.match.params.id)
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    this.setState({details: res.data});
                }
            })
    }

    hoverImage(imgId, e) {
        let temp = JSON.parse(JSON.stringify(this.state.dynamicUI));
        temp.display = 'block';
        temp.left = e.clientX - e.target.offsetLeft+10;
        temp.top = e.clientY - e.target.offsetTop+20;

        this.setState({dynamicUI: temp});

        this.setState({pointer: {cursor: 'pointer'}});

        if(imgId)
            this.setState({selected: imgId});
    }

    hideMenu() {
        let temp = JSON.parse(JSON.stringify(this.state.dynamicUI));
        temp.display = 'none';

        this.setState({dynamicUI: temp});
        this.setState({pointer: {cursor: 'default'}});
    }

    toggleSlideshow() {
        if(this.state.startSlideshow)
            this.setState({startSlideshow: false});
        else
            this.setState({startSlideshow: true});
        
    }

    redirectImage() {
        if(this.state.selected) this.setState({redirect: true});
    }

    render(){

        if(this.state.redirect) {
            return <Redirect to={'/images/view/' + this.state.details.images[this.state.selected].imgId} />;
        }

        return (
            <div onMouseMove={this.hideMenu} style={this.state.pointer}>
                <Header></Header>

                <span style={this.state.dynamicUI} className={styles.hover}>Title</span>
                <button className={`${styles.startSlideshow}  ${this.state.startSlideshow ? styles.pause:''}`} onClick={this.toggleSlideshow}> Слайдшоу</button>
                <button className={styles.info} onClick={this.redirectImage}>Инфо</button>
                
                <ThreeContainer 
                    edit={false}
                    details={this.state.details}
                    hoverImage={this.hoverImage}
                    startSlideshow={this.state.startSlideshow} >
                </ThreeContainer>
            </div>
        )
    }
}