import React from 'react'
import '../../styles/dashboard.css'
import withLogging from '../../hocs/withLogging.js'
import Header from '../Header.js'
import ImageItem from './ImageItem.js'
import {withRouter} from 'react-router-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'


class MyImages extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {images: [], url: false};
    }

    componentDidMount(){
        this.getImages();
    }

    getImages = () => {
        requester.get('/api/images/user')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    console.log(res.images);
                    this.setState({
                        images: res.images
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }

    viewImage = (url) => {
        this.props.history.push('/images/view/' + url);
    }

    editImage = (url) => {
        this.props.history.push('/image/details/' + url);
    }

    deleteImage = (id) => {
        requester.remove('/api/images/' + id).then(res => {
            this.getImages();
            observer.fire('notifications', res);
        });
    }

    render() {
        let images = this.state.images;

        return (
            <div>
                <Header transparent={true}></Header>
                
                <div className='expo-content2'>
                    <h1>Всички изображения</h1>
                    <hr />
                    <ul>
                        {
                            images.map((img, i) => 
                            <ImageItem 
                                    src={img ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + img.src:''} 
                                    title={img.title} 
                                    key={i} 
                                    viewImage={() => {this.viewImage(img.id)}}
                                    editImage={() => {this.editImage(img.id)}}
                                    deleteImage={() => {this.deleteImage(img.id)}}
                                    isAdmin={this.props.isAdmin}
                                    isAuthor={this.props.images.includes(img.id)}>
                            </ImageItem>)
                        }
                    </ul>
                    <hr />
                </div>
            </div>
        );

    }
}
export default withRouter(withLogging(MyImages));