import React from 'react'
import styles from '../../styles/exposition.module.css'
import {Redirect} from 'react-router-dom'
import withLogging from '../../hocs/withLogging.js'

import Header from '../Header'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

import room1 from '../../resources/room1.png'
import room2 from '../../resources/room2.png'
import room3 from '../../resources/room3.png'

class Details extends React.Component {
    constructor(props){
        super(props);

        this.state = {expo:{title: '', description: '', from: Date(), to: Date(), images: [], room: 0}, redirect: false, images: [], ids: []};
        
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount(){
        if(this.props.match.params.id) {
            requester.get('/api/exposition/details/' + this.props.match.params.id)
                .then(res => res.json())
                .then(res => {
                    if(res.type !== 'error'){
                        console.log(res.data);
                        this.setState({expo: res.data});
                    }
                    else
                        observer.fire('notifications', res);
                });
        }

        requester.get('/api/images/user')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    this.setState({
                        images: res.images,
                        ids: res.ids
                    });
                }
                else
                    observer.fire('notifications', res);
            })
    }

    onSubmit(event){
        event.preventDefault();
        if(!this.state.expo.title){
            observer.fire('notifications', {
                type: 'error',
                message: 'Please fill all of important fields'
            })
        } else {
            requester.post(this.state.expo, '/api/expositions')
                .then(res => res.json())
                .then(res => {
                    if(res.type !== 'error') {
                        this.setState({redirect: true});
                    } else {
                        observer.fire('notifications', {
                            type: res.type,
                            message: res.message
                        });
                    }
                });
        }

    }

    onChange(event){
        let name = event.target.name;
        let val = event.target.value;

        let expo = this.state.expo;
        expo[name] = val;
        this.setState({
            expo
        });
        console.log(this.state);
    }

    render(){
        if(!this.props.logging){
            return <Redirect to='/' />;
        }
        if(this.state.redirect){
            return <Redirect to='/dashboard' />;
        }

        let date = new Date().toISOString().substr(0, 10);

        return (
            <div>
                <Header transparent={true}></Header>
                <div className={styles.content}>
                    
                    <form className={styles.form} onSubmit={this.onSubmit}> 
                        <label>Заглавие</label>
                        <input type='text' name='title' className={styles.input} defaultValue={this.state.expo.title} onChange={this.onChange}/>
                        <br/>

                        <label>Продължителност</label>
                        <br/>
                        <div>
                        <label className={styles.duration}>От
                            <input type='date' name='from' onChange={this.onChange} defaultValue={date} />
                        </label>
                        <label className={styles.duration}>До
                            <input type='date' name='to' onChange={this.onChange} defaultValue={date} />
                        </label>
                        </div>
                        <br/><br/>
                        <label>Описание</label>
                        <textarea className={styles.textarea} name='description' value={this.state.expo.description} onChange={this.onChange}></textarea>
                        <label>Заглавната снимка</label>
                        <br/><br/><br/><br/>

                        <label className={styles.horizontalScroll}>
                        {
                            this.state.images.map((img, i) => <div key={i+100} className={styles.rotated}>
                                                                <label>
                                                                    <input type='radio' name='mainImage' value={img.src} onChange={this.onChange} />
                                                                    <img src={'http://res.cloudinary.com/dvn7y9omc/image/upload/' + img.src} alt='could not load'/>
                                                                </label>
                                                            </div>)
                        }
                        </label>
                        <br/><br/><br/><br/><br/><br/>

                        <label>3D изглед</label>
                        <br/><br/><br/>
                        <label>
                            <input type='radio' name='room' value={1} onChange={this.onChange} />
                            <img src={room1} alt='could not load'/>
                        </label>

                        <label>
                            <input type='radio' name='room' value={2} onChange={this.onChange} />
                            <img src={room2} alt='could not load'/>
                        </label>

                        <label>
                            <input type='radio' name='room' value={3} onChange={this.onChange} />
                            <img src={room3} alt='could not load'/>
                        </label>

                        <button type='submit' className='file-button'>Запази</button>
                    </form>
                </div>
                
            </div>
        )
    }
}

export default withLogging(Details);