import React from 'react'
import FileDrop from 'react-file-drop'
import '../../styles/popup.css'
import requester from '../../helpers/requester'
import observer from '../../helpers/observer'
import cloud from '../../resources/cloud.png'
import {Redirect} from 'react-router-dom'

export default class Popup extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            redirect: false
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.handleDrop = this.handleDrop.bind(this);
    }
    onChange(event){
        this.onSubmit(event.target.files[0]);
    }
    onSubmit(file){

        let formData = new FormData();
        formData.append('images', file);

        observer.fire('notifications', {
            type: 'loading',
            message: 'Моля изчакайте!'
        });

        requester.postImage(formData, '/api/images')
            .then(res => res.json())
            .then(res => {
                observer.fire('notifications', {
                    type: res.type,
                    message: res.message
                });
                console.log(res);
                this.setState({redirect: true, id: res.id});
            }
            );
    }

    handleDrop(files, event) {
        this.onSubmit(files[0]);
    }
    render(){
        let item = this.props.item;
        let popupStyle = 'hide-popup';
        if(this.props.display)
            popupStyle = 'show-popup';

        if(this.state.redirect) return(<Redirect to={'/image/details/' + this.state.id} />)

        return (
            <div className={'modal ' + popupStyle}>

                <div className='modal-content'>
                    <div>
                        <span className='close' onClick={()=>this.props.togglePopup()}>&times;</span>
                    </div>
                    <div className='modal-body'>
                            <FileDrop onDrop={this.handleDrop} className='file-drop'>
                                <img src={cloud}></img>
                                <span>
                                    <h2>Дръпни и пусни файл</h2>
                                    <h3>или</h3>
                                    <form onSubmit={this.onSubmit}>
                                        <label className='file-button'> избери файлове за качване
                                            <input type='file' onChange={this.onChange} name='image'></input>
                                        </label>
                                    </form>
                                </span>    
                            </FileDrop>
                    </div>
                </div>

            </div>
        )
    }
}