import React from 'react'
import '../../styles/dashboard.css'
import withLogging from '../../hocs/withLogging.js'
import Header from '../Header.js'
import ExpoItem2 from '../exposition/ExpoItem2.js'
import {Redirect, withRouter} from 'react-router-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'


class Search extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {expositions: [], url: ''};
    }

    componentDidMount(){
        console.log(this.props.match.params.name);
        requester.get('/api/expositions/search/' + this.props.match.params.name)
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    console.log(res.data);
                    this.setState({
                        expositions: res.data
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }

    exposition3D = (url) => {
        this.setState({url});
    }

    deleteExpo = (id) => {
        requester.remove('/api/expositions/' + id).then(res => {
            this.getExpositions();
            observer.fire('notifications', res);
        });
    }
    
    addTop = (id) => {
        requester.post({id}, 'api/expositions/top');
    }

    render() {
        if(this.state.url)
        return <Redirect to={'/exposition/view/' + this.state.url}/>;

        let expositions = this.state.expositions;
        //let images = this.state.images;

        return (
            <div>
                <Header transparent={true}></Header>
                
                <div className='expo-content2'>
                    <h1>Резултати от {this.props.match.params.name}</h1>
                    <hr />
                    <ul>
                        {
                            expositions.map((expo, i) => 
                            <ExpoItem2 
                                    src={expo ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + expo.mainImage:''} 
                                    title={expo.title} 
                                    key={i} 
                                    addTop={() => {this.addTop(expo.id)}}
                                    expo3D={() => {this.exposition3D(expo._id)}}
                                    deleteExpo={() => {this.deleteExpo(expo.id)}}
                                    isAdmin={this.props.isAdmin}
                                    isAuthor={this.props.expositions.includes(expo.id)}>
                            </ExpoItem2>)
                        }
                    </ul>
                    <hr />
                </div>
            </div>
        );

    }
}
export default withRouter(withLogging(Search));