import React from 'react';
import * as THREE from 'three';
import * as OBJLoader from 'three-obj-loader';
import * as MTLLoader from 'three-mtl-loader';
import { OrbitControls } from 'three-orbitcontrols';
import wood from '../../resources/wood.jpg';
import dino from '../../resources/dino.jpg';
import bird from '../../resources/bird.jpg';
import obj1 from '../../resources/models/room1.obj';
import museum from '../../resources/models/museum.obj';
import museumMtl from '../../resources/models/T-Rex Model.mtl';
import obj2 from '../../resources/models/room3.obj';
import obj3 from '../../resources/models/room2.obj';
OBJLoader(THREE);

class ThreeContainer extends React.Component{
  constructor(props){
    super(props);
    this.loaded = false;
    this.slideShow = [];
    this.slideIndex = 0;
    this.loadObj = this.loadObj.bind(this);
    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
    this.animate = this.animate.bind(this);
    this.renderScene = this.renderScene.bind(this);
  }
componentWillReceiveProps(nextProps) {
  if(nextProps.details && !this.loaded) {
    this.maxIndex=0;
    if(nextProps.details.images){
    let images = nextProps.details.images;
      for(let i=0;i<images.length;i++) {
        if(images[i] != null){
          let src = images[i].src;
          let position = src.indexOf('upload') + 6;
          src = [src.slice(0, position), '/w_256', src.slice(position)].join('');
          this.loadPicture(src, images[i].matrix, i);

          this.maxIndex++;
        }
      }

    let arr = [museum, obj1, obj2, obj3];
    this.room = arr[nextProps.details.room];
    
    //ADD OBJ
    this.loader = new this.THREE.OBJLoader();
    this.loader.load(
      this.room,
      // called when resource is loaded
      this.loadObj,
      // called when loading is in progresses
      function (xhr) {
        console.log((xhr.loaded / xhr.total * 100) + '% loaded' );
      },
      // called when loading has errors
      function (error) {
        console.log( 'An error happened' + error);
      }
    );
    this.loaded = true;
  }
  }
}
componentDidMount(){
    //Event listeners
    document.addEventListener('mousemove', this.onMouseMove, false);
    document.addEventListener('mousedown', this.onMouseDown, false);
    document.addEventListener('mouseup', this.onMouseUp, false);

    this.THREE = THREE;
    const width = this.mount.clientWidth;
    const height = this.mount.clientHeight;

    //Create SCENE
    let scene = new THREE.Scene();
    this.scene = scene;

    //ADD CubeMap
    this.scene.background = new THREE.CubeTextureLoader()
        .setPath( 'https://res.cloudinary.com/dvn7y9omc/image/upload/v1555523069/cubemap/' )
        .load( [
          'px.png',
          'nx.png',
          'py.png',
          'ny.png',
          'pz.png',
          'nz.png'
        ] );

    //ADD CAMERA
    this.camera = new THREE.PerspectiveCamera(
      75,
      width / height,
      0.1,
      3000
    );
    this.camera.position.set(100, 100, 100);
    this.lastPos = this.camera.position;

    //ADD Raycaster
    this.raycaster = new THREE.Raycaster();
    this.mouse = new THREE.Vector2();

    //ADD RENDERER
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setClearColor('rgb(0,0,0)');
    this.renderer.setSize(width, height);
    this.mount.appendChild(this.renderer.domElement);

    //ADD CONTROLS
    this.controls = new this.THREE.OrbitControls( this.camera, this.renderer.domElement );
    this.controls.target.set(100.01,100,100.01);
    this.lastTarget = this.controls.target;
    this.controls.enableZoom = false;
    this.controls.panSpeed = 3000.0;
    this.controls.rotateSpeed = 0.08;
    this.controls.enableDamping = true;
    this.controls.dampingFactor = 0.1;
    

    

    //ADD Lights
    let light = new THREE.PointLight( 'rgb(255,255,255)', 1.3, 10000 );
    light.position.set( -1500, 400, -1500 );
    this.scene.add( light );

    let light2 = new THREE.AmbientLight( 'rgb(255,255,255)', 0.2, 2000 );
    light2.position.set( 0,0,0 );
    this.scene.add( light2 );

    let light3 = new THREE.PointLight( 'rgb(255,255,255)', 1, 10000 );
    light3.position.set( 2000, 400, 1500 );
    this.scene.add( light3 );

    //Start timer
    this.time = 0;
    this.start();
}

loadPicture(src, matrix, index) {
  //ADD Frame

  let textureLoader = new THREE.TextureLoader();
  let map = textureLoader.load(src, (tex) => {
    let dif = tex.image.width/tex.image.height;
    let geometry = new THREE.BoxGeometry( 100 * dif, 100, 6 );

    let material = new THREE.MeshPhongMaterial({map: map, side: THREE.FrontSide});
    material.transparent = true;
  
    let mesh = new THREE.Mesh( geometry, material );

    mesh.position.fromArray(matrix.position);
    mesh.rotation.fromArray(matrix.rotation);
    mesh.name = index;

    this.slideShow.push(matrix);

    this.scene.add( mesh );
  } );
  
  
}

addPicture(material, intersects, src, id) {
  //ADD Frame
  let geometry = new THREE.BoxGeometry( 100, 100, 5 );
  let mesh = new THREE.Mesh( geometry, material );
  mesh.position.set(intersects[0].point.x + intersects[0].face.normal.x*3, 
        intersects[0].point.y + intersects[0].face.normal.y*3, 
        intersects[0].point.z + intersects[0].face.normal.z*3);

  mesh.normal = [];
  mesh.normal.x = intersects[0].face.normal.x;
  mesh.normal.y = intersects[0].face.normal.y;
  mesh.normal.z = intersects[0].face.normal.z;
  
  mesh.rotation.x = (intersects[0].face.normal.y) *  Math.PI / 2;
  mesh.rotation.y = -(intersects[0].face.normal.x) * Math.PI / 2;
  mesh.name = this.maxIndex+1;
  this.maxIndex++;

  this.scene.add( mesh );

  this.props.saveExpo(mesh.name, src, id, mesh.getWorldPosition(), mesh.rotation, mesh.normal);
}

componentWillUnmount(){
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
}

start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);
    }
}

stop() {
    cancelAnimationFrame(this.frameId);
}

distanceVector( v1, v2 )
{
    let dx = v1.x - v2.x;
    let dy = v1.y - v2.y;
    let dz = v1.z - v2.z;

    return Math.sqrt( dx * dx + dy * dy + dz * dz );
}

checkIntersection(intersects) {
  if(intersects.length > 0) {
    if(this.distanceVector(intersects[0].point, this.camera.position) <= 30){
      this.hit = true;
    }
  }
}

animate() {
  this.controls.update();
  this.hit = false;
  
  //Check if there is object to remove
  if(this.props.removing && this.props.edit){
    this.scene.remove(this.scene.getObjectByName(this.props.removing));
  }

  //Check if camera has to move
  if(this.newCameraPosition && !this.props.startSlideshow) {

    this.camera.position.x = this.lerp(this.camera.position.x, this.newCameraPosition.x, this.time);
    this.camera.position.y = this.lerp(this.camera.position.y, this.newCameraPosition.y, this.time);
    this.camera.position.z = this.lerp(this.camera.position.z, this.newCameraPosition.z, this.time);
    
    this.controls.target.x = this.lerp(this.controls.target.x, this.newCameraRotation.x, this.time);
    this.controls.target.y = this.lerp(this.controls.target.y, this.newCameraRotation.y, this.time);
    this.controls.target.z = this.lerp(this.controls.target.z, this.newCameraRotation.z, this.time);

    if(this.time >= 0.5) {
      this.time = 0;
      this.newCameraPosition = null;
    } else this.time += 0.01;
  }

  if(this.props.startSlideshow) {
    let pos = this.slideShow[this.slideIndex].position;
    let rot = this.slideShow[this.slideIndex].normal;

    this.camera.position.x = this.lerp(this.camera.position.x, pos[0] + rot.x*100, this.time);
    this.camera.position.y = this.lerp(this.camera.position.y, pos[1], this.time);
    this.camera.position.z = this.lerp(this.camera.position.z, pos[2] + rot.z*100, this.time);
    
    this.controls.target.x = this.lerp(this.controls.target.x, pos[0] + rot.x*99.99, this.time);
    this.controls.target.y = this.lerp(this.controls.target.y, pos[1] + 0.001, this.time);
    this.controls.target.z = this.lerp(this.controls.target.z, pos[2] + rot.z*99.99, this.time);

    if(this.time >= 0.5) {
      this.slideIndex++;
      if(this.slideIndex >= this.maxIndex) this.slideIndex = 0;
      this.time = 0;
    } else this.time += 0.002;
  }

   // calculate objects intersecting the picking ray
   if(this.scene.getObjectByName('scene') && !this.props.startSlideshow){
    let dir = false;
    this.raycaster.set(this.camera.position, new THREE.Vector3(0,0,-1));
    let intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );
    this.checkIntersection(intersects);

    this.raycaster.set(this.camera.position, new THREE.Vector3(0,0,1));
    intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );
    this.checkIntersection(intersects);

    if(this.hit) dir = true;

    this.raycaster.set(this.camera.position, new THREE.Vector3(1,0,0));
    intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );
    this.checkIntersection(intersects);

    this.raycaster.set(this.camera.position, new THREE.Vector3(-1,0,0));
    intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );
    this.checkIntersection(intersects);

    if(this.hit){
      if(dir){
        this.camera.position.z = this.lastPos.z;
        this.controls.target.z = this.lastTarget.z;
      }
      else{
        this.camera.position.x = this.lastPos.x;
        this.controls.target.x = this.lastTarget.x;
      }
    } else {
      this.controls.panSpeed = 3000.0;
      this.lastPos = new THREE.Vector3(this.camera.position.x, this.camera.position.y, this.camera.position.z);
      this.lastTarget = new THREE.Vector3(this.controls.target.x, this.controls.target.y, this.controls.target.z);
    }
   } 
   

   this.mouse.lastX = this.mouse.x;
   this.mouse.lastY = this.mouse.y;
   this.renderScene();
   this.frameId = window.requestAnimationFrame(this.animate);
}

renderScene() {
  this.renderer.render(this.scene, this.camera);
}

loadObj(object) {
  let material = new THREE.MeshPhongMaterial({ color: 'rgb(255,255,255)'});
  object.name = 'scene';
  
    // instantiate a loader
    let textureLoader = new THREE.TextureLoader();
    let woodMap = textureLoader.load(wood);
    woodMap.wrapS = woodMap.wrapT = THREE.RepeatWrapping;
    woodMap.repeat.set( 20, 20 );

    let woodTexture = new THREE.MeshPhongMaterial({map: woodMap});

    
    let dinoMap = textureLoader.load(dino);
    dinoMap.wrapS = dinoMap.wrapT = THREE.MirroredRepeatWrapping;
    dinoMap.mapping = THREE.UVMapping;
    let dinoTexture = new THREE.MeshPhongMaterial({map: dinoMap});
    dinoTexture.flatShading = false;

    let birdMap = textureLoader.load(bird);
    let birdTexture = new THREE.MeshPhongMaterial({map: birdMap});
  
  object.traverse( function ( child ) {

    if ( child instanceof THREE.Mesh ) {

      switch (child.name){
        case 'wood':
          child.material = woodTexture;
          break;
        case 'Dino':
          child.geometry.computeFaceNormals();
          child.material = dinoTexture;
          break;
        case 'Cearadactylus':
          child.material = birdTexture;
          break;
        default: 
          child.material = material;
          break;
      }

    }

  } );

  this.scene.add(object.getObjectByName('Dino'));
  object.remove(object.getObjectByName('Dino'));

  this.scene.add(object.getObjectByName('Cearadactylus'));
  object.remove(object.getObjectByName('Cearadactylus'));
  
  this.scene.add(object);
}

lerp(a, b, t ) {
  return a * ( 1 - t ) + b * t;
}

onMouseMove = (e) => {
  this.mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
  this.mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;

    //Raycast from camera
    this.raycaster.setFromCamera( this.mouse, this.camera );
    let intersects = this.raycaster.intersectObjects( this.scene.children, true );

    if(intersects.length > 0) {

      //Check if image is clicked
      console.log(intersects[0].object.name);
      if(!isNaN(intersects[0].object.name)) {
        this.selected = intersects[0].object;
        this.selected.normal = {};

        //Send coordinates for UI
        if(this.click && !this.moving && this.props.edit) {
          this.props.chosenImage(intersects[0].object.name, e);

          this.moving = true;
          this.controls.enabled = false;
        }
        else if(this.click) {
          this.props.hoverImage(intersects[0].object.name, e);
          
          // Setup smooth camera transition
          this.newCameraPosition = {
            x: intersects[0].object.position.x + intersects[1].face.normal.x*100,
            y: intersects[0].object.position.y,
            z: intersects[0].object.position.z + intersects[1].face.normal.z*100
          }
          this.newCameraRotation = {
            x: intersects[0].object.position.x + intersects[1].face.normal.x*99.99,
            y: intersects[0].object.position.y + 0.001,
            z: intersects[0].object.position.z + intersects[1].face.normal.z*99.99
          }

          this.click = false;
        }
        else if(!this.props.edit) {
          this.props.hoverImage(intersects[0].object.name, e);

          this.controls.enabled = true;
        }
        
      }
    }

  if(this.moving && this.scene.children[2] && this.props.edit) {
    this.raycaster.setFromCamera( this.mouse, this.camera );
    let intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );

    if(intersects.length > 0) {
      //Set position based on the normal
      this.selected.position.set(intersects[0].point.x + intersects[0].face.normal.x*3, 
        intersects[0].point.y + intersects[0].face.normal.y*3, 
        intersects[0].point.z + intersects[0].face.normal.z*3);

      this.selected.normal.x = intersects[0].face.normal.x;
      this.selected.normal.y = intersects[0].face.normal.y;
      this.selected.normal.z = intersects[0].face.normal.z;
      console.log(this.selected);

      this.selected.rotation.x = (intersects[0].face.normal.y) *  Math.PI / 2;
      this.selected.rotation.y = (intersects[0].face.normal.x) * Math.PI / 2;

      //Send coordinates for UI
      this.props.chosenImage('', e);
    }
  }

  this.click = false;
}
onMouseDown = (e) => {
  if(!this.moving && this.props.edit) 
    setTimeout(() => {
      this.props.hideUI();
    }, 100);
  if(this.props.edit) 
    this.click = true;

  this.flag = true;
  setTimeout(() => {
    this.flag = false;
  }, 100);
}

onMouseUp = (e) => {
  if(this.moving && this.props.edit){
    this.props.saveExpo(this.selected.name, null, null, this.selected.getWorldPosition(), this.selected.rotation, this.selected.normal);
  }

  if(!this.props.edit && this.flag) 
    this.click = true;
  else
    this.click = false;

  this.moving = false;
  this.controls.enabled = true;
}

dropped = (e) => {
  e.preventDefault();

  //Set mouse position
  this.mouse.x = ( e.clientX / window.innerWidth ) * 2 - 1;
  this.mouse.y = - ( e.clientY / window.innerHeight ) * 2 + 1;
  
  //Start raycasting
  this.raycaster.setFromCamera( this.mouse, this.camera );
  
  if(this.scene.getObjectByName('scene')){
    let intersects = this.raycaster.intersectObjects( this.scene.getObjectByName('scene').children );
    
    //Add new picture
    if(intersects.length > 0) {
      let textureLoader = new THREE.TextureLoader();
      let map = textureLoader.load(e.dataTransfer.getData('text/plain'));
      let material = new THREE.MeshPhongMaterial({map: map});

      this.addPicture(material, intersects, e.dataTransfer.getData('text/plain'), e.dataTransfer.getData('text/id'));
      
    }
  }
}

render(){
    return(
      <div
        style={{ width: '100%', height: '100vh' }}
        ref={(mount) => { this.mount = mount }}
        onDragOver={(e) => {
          e.preventDefault();
          return false;
        }}
        onDrop={this.dropped}
      />
    )
  }
}
export default ThreeContainer