import React from 'react'
import '../styles/header.css'
import logo from '../resources/logo2.png'
import profile from '../resources/profile.png'
import withLogging from '../hocs/withLogging.js'
import Menu from './Menu'
import Notification from './Notification'
import {Link, Redirect} from 'react-router-dom'
import requester from '../helpers/requester'
import observer from '../helpers/observer'
import Popup from './modal/Popup.js'

class Header extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {showHeader: props.transparent, toggleHeader: false, text: '', index: 0, redirect: false, display: false};
        this.lastScroll = 0;

        this.showHeader = this.showHeader.bind(this);
        this.logout = this.logout.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.togglePopup = this.togglePopup.bind(this);
        this.toggleHeader = this.toggleHeader.bind(this);
    }
    componentDidMount() {
        window.addEventListener('scroll', this.showHeader);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.showHeader);
    }

    showHeader() {
        let scroll = window.pageYOffset;
        if(scroll >= 10) this.setState({showHeader: true});
        else if(!this.props.transparent) this.setState({showHeader: false});
    }

    logout(event) {
        event.preventDefault();
        requester.get('/logout')
            .then(() => {
                localStorage.removeItem('username');
                observer.fire('logging', false);
            });
    }
    
    onChange(event){
        let name = event.target.name;
        let val = event.target.value;

        this.setState({
           [name]: val
        });
    }

    togglePopup(event) {
        if(event) event.preventDefault();
        let display = !this.state.display;
        this.setState({display});
    }

    toggleHeader() {
        let temp = this.state.toggleHeader;
        this.setState({toggleHeader: !temp});
    }

    onSubmit(event){
        event.preventDefault();
        if(this.state.name)
        if(window.location.href.startsWith('http://www.vshow.eu/search/')) {
            window.location.replace('http://www.vshow.eu/search/' + this.state.name);
        } else if(window.location.href.startsWith('http://localhost:3000/search/')) {
            window.location.replace('http://localhost:3000/search/' + this.state.name);
        } else {
            this.setState({redirect: true});
        }

    }

    render() {
        if(this.state.redirect)
            return <Redirect to={'/search/' + this.state.name}/>;

        let showHeader = this.state.showHeader ? '':'nav-up';

        if(this.state.toggleHeader) showHeader = 'open';

        if(!this.props.logging){
            return (
                <div>
                    <Notification />
                    <header className={showHeader}>
                    <span><Link to='/'><img src={logo} className='logo'></img></Link></span>
                    <Menu toggleHeader={this.toggleHeader}></Menu>
                    <form  onSubmit={this.onSubmit}>
                        <input type='text' placeholder='Search..' name='name' onChange={this.onChange}></input>
                        <button type='submit'>
                            <i className='fa fa-search'></i>
                        </button>
                    </form>
                    <Link to='/login'>Вход/Регистрация</Link>
                    </header>
                </div>
            );
        }
        else {
            return (
                <div>
                    <Notification />
                    <Popup display={this.state.display} togglePopup={this.togglePopup}></Popup>
                    <header className={showHeader}>
                    <span><Link to='/'><img src={logo} className='logo'></img></Link></span>

                    <Menu toggleHeader={this.toggleHeader}></Menu>
                    <form  onSubmit={this.onSubmit}>
                        <input type='text' placeholder='Search..' name='name' onChange={this.onChange}></input>
                        <button type='submit'>
                            <i className='fa fa-search'></i>
                        </button>
                    </form>
                    <Link to='' onClick={this.logout}>Изход</Link>
                    <Link to='/dashboard'><img src={profile} id='profile'></img></Link>
                    <Link to='' onClick={this.togglePopup}>Качване</Link>
                    
                    </header>
                </div>
            );
        }

    }
}
export default withLogging(Header);