import React from 'react'
import ReactDOM from 'react-dom'
import styles from '../../styles/details.module.css'
import {Redirect} from 'react-router-dom'
import withLogging from '../../hocs/withLogging.js'

import Header from '../Header'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

class Details extends React.Component {
    constructor(props){
        super(props);

        this.state = {img:{title: '', price: 0, description: '', year: 2019, genre: '', currency: 'lv'}, redirect: false};
        
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount(){
        requester.get('/api/images/details/' + this.props.match.params.id)
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    console.log(res.data);
                    this.setState({img: res.data});
                }
                else
                    observer.fire('notifications', res);
            })
    }

    onSubmit(event){
        event.preventDefault();
        if(!this.state.img.title){
            observer.fire('notifications', {
                type: 'error',
                message: 'Please fill all of important fields'
            })
        } else {
            requester.update(this.state.img, '/api/images')
                .then(res => res.json())
                .then(res => {
                    if(res.type === 'info') {
                        this.setState({redirect: true});
                    }
                    observer.fire('notifications', {
                        type: res.type,
                        message: res.message
                    });
                });
        }

    }

    onChange(event){
        let name = event.target.name;
        let val = event.target.value;

        let img = this.state.img;
        img[name] = val;
        this.setState({
           img
        });
        console.log(this.state);
    }

    deleteImage(id) {
        this.setState({
            redirect: true
        });
        requester.remove('/api/images/' +  id);
    }

    render(){
        if(!this.props.logging){
            return <Redirect to='/' />;
        }
        if(this.state.redirect){
            return <Redirect to='/dashboard' />;
        }

        return (
            <div>
                <Header transparent={true}></Header>
                <div className={styles.content}>
                    <img src={this.state.img ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + this.state.img.id:''} className={styles.img}></img>
                    
                    <form className={styles.form} onSubmit={this.onSubmit}> 
                        <label>Заглавие</label>
                        <input type='text' name='title' className={styles.input} defaultValue={this.state.img.title} onChange={this.onChange}/>
                        <br/>
                        <label>Цена
                        <input type='number' name='price' className={styles.half_box} value={this.state.img.price} onChange={this.onChange}/>
                        </label>
                        <label>Валута
                        <select name='currency' value={this.state.img.currency}  onChange={this.onChange}>
                            <option value='lv'>lv.</option>
                            <option value='euro'>€</option>
                        </select>
                        </label>
                        <br/><br/><br/><br/>

                        <label>Година</label>
                        <input type='number' name='year' className={styles.input} defaultValue={this.state.img.year} onChange={this.onChange}/>
                        <br/>
                        <label>Жанр</label>
                        <br/>
                        <br/>
                        <input type='text' name='genre' className={styles.input} defaultValue={this.state.img.genre} onChange={this.onChange}/>
                        <br/>
                        <label>Описание</label>
                        <textarea className={styles.textarea} name='description' value={this.state.img.description} onChange={this.onChange}></textarea>
                        <button type='submit' className='file-button'>Запази</button>
                        <button className='file-button' onClick={(e) => {e.preventDefault(); this.deleteImage(this.state.img._id)}}>Изтрий</button>
                    </form>
                </div>
                
            </div>
        )
    }
}

export default withLogging(Details);