import React from 'react'
import ReactDOM from 'react-dom'
import styles from '../../styles/details.module.css'
import {Redirect} from 'react-router-dom'
import withLogging from '../../hocs/withLogging.js'

import Header from '../Header'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

class Details extends React.Component {
    constructor(props){
        super(props);

        this.state = {img:{title: '', price: 0, description: '', year: 2019, genre: '', currency: 'lv'}};
        
    }

    componentDidMount(){
        requester.get('/api/images/details/' + this.props.match.params.id)
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    console.log(res.data);
                    this.setState({img: res.data});
                }
                else
                    observer.fire('notifications', res);
            })
    }

    render(){
        
        return (
            <div>
                <Header transparent={true}></Header>
                <div className={styles.content}>
                    <img src={this.state.img ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + this.state.img.id:''} className={styles.img}></img>

                    <h1>{this.state.img.title}</h1>
                    <h4>Цена {this.state.img.price}</h4>
                    <label>{this.state.img.currency}</label>

                    <h4>Година {this.state.img.year}</h4>
                    <h4>Жанр {this.state.img.genre}</h4>
                    
                    <h4>Описание</h4>
                    <h4>{this.state.img.description}</h4>
                </div>
                
            </div>
        )
    }
}

export default withLogging(Details);