import React from 'react'
import '../../styles/dashboard.css'
import {Redirect, Link, withRouter} from 'react-router-dom'

const ImageItem = ({src, title, viewImage, editImage, isAdmin, isAuthor, deleteImage}) => (
    <li className='expo-item2'>
        <img src={src} className='expo-img2' onClick={viewImage}></img>
        <h2 className='title'>{title}</h2>
        <button className='expandbtn inline' onClick={viewImage}><span>Преглед</span></button>
        <button className='expandbtn inline' onClick={editImage}><span>Редактирай</span></button>
        {(isAdmin || isAuthor) ? <button className='expandbtn inline' onClick={deleteImage}><span>Изтрий</span></button>:''}
    </li>
);

export default ImageItem;