import React from 'react'
import '../../styles/dashboard.css'
import withLogging from '../../hocs/withLogging.js'
import Header from '../Header.js'
import ExpoItem from './ExpoItem.js'
import Popup from '../modal/Popup.js'
import {Redirect, Link, withRouter} from 'react-router-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'

import plus from '../../resources/add.png'

class Dashboard extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {display: false, expositions: [], images: [], url: false};
    }

    componentDidMount(){
        requester.get('/api/images/user')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    let images = this.state.images;
                    images = res.images;
                    let size = images.length;

                    if(images.length <= 5)
                        for(let i=0;i<5-size; i++) images.push('');

                    this.setState({
                        images: images,
                        ids: res.ids
                    });
                }
                else
                    observer.fire('notifications', res);
            })

        requester.get('/api/expositions/user')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    let expos = this.state.expositions;
                    expos = res.expositions;
                    let size = expos.length;

                    console.log(expos);
                    if(expos.length <= 5)
                        for(let i=0;i<5-size; i++) expos.push('');

                    this.setState({
                        expositions: expos
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }

    showAllExpos = () => {
        return <Redirect to='/expositions/user' />;
    }

    showAllImages = () => {
        return <Redirect to='/myimages' />;
    }

    togglePopup = () => {
        let display = !this.state.display;
        this.setState({display});
    }

    exposition = () => {
        this.props.history.push('exposition/details/');
    }

    exposition3D = (url) => {
        this.props.history.push('exposition/edit/' + url);
    }

    details = (url) => {
        this.props.history.push('image/details/' + url);
    }

    render() {
        if(!this.props.logging){
            return <Redirect to='/' />;
        }

        let expositions = this.state.expositions;
        let images = this.state.images;

        return (
            <div className='dashboard'>
                <Header transparent={true}></Header>
                <Popup display={this.state.display} togglePopup={this.togglePopup}></Popup>
                
                {
                    this.props.isAdmin ? <button className='expandbtn top break'><span><Link to='/expositions'>Избери най-добрите</Link></span></button>:''
                }
                <div className='expo-content'>
                    <h1>Моите експозиции</h1>
                    <hr />
                    <ul>
                        <ExpoItem src={plus} title='' click={this.exposition}  key={101}></ExpoItem>
                        {
                            expositions.map((expo, i) => 
                                <ExpoItem 
                                    src={expo ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + expo.mainImage:''} 
                                    title='' 
                                    key={i} 
                                    click={()=>{ if(expo) this.exposition3D(expo.id)}}>
                                </ExpoItem>)
                        }
                    </ul>
                    <button className='expandbtn'><span><Link to='/expositions/user'>Покажи всички</Link></span></button>
                    <hr />
                </div>
                <div className='expo-content'>
                    <h1>Моите изображения</h1>
                    <hr />
                    <ul>
                        <ExpoItem src={plus} title='' click={this.togglePopup}  key={100}></ExpoItem>
                        {
                            images.map((img, i) => 
                                <ExpoItem 
                                    src={img ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + img.src:''} 
                                    title='' 
                                    key={i+10}
                                    click={()=>{if(img) this.details(this.state.ids[i])}}>
                                </ExpoItem>)
                        }
                    </ul>
                    <button className='expandbtn'><span><Link to='/images/user'>Покажи всички</Link></span></button>
                    <hr />
                </div>
            </div>
        );

    }
}
export default withRouter(withLogging(Dashboard));