import React from 'react'
import {Route, Switch} from 'react-router-dom'
import Home from './home/Home'
import Login from './registration/Login.js'
import Register from './registration/Register.js'
import Dashboard from './Dashboard/Dashboard.js'
import NotFound from './NotFound.js'

import ExpositionEdit from './exposition/ExpositionEdit.js'
import ExpositionView from './exposition/ExpositionView.js'
import ExpoDetails from './exposition/ExpoDetails.js'
import AllExpositions from './exposition/AllExpositions.js'
import MyExpositions from './exposition/MyExpositions.js'

import Details from './images/ImageDetails.js'
import MyImages from './images/MyImages.js'
import ImageView from './images/ImageView.js'

import Search from './Search/Search.js'
const Main = () => (
    <main>
        <Switch>
            <Route exact path='/' component={Home} />
            <Route exact path='/login' component={Login} />
            <Route exact path='/register' component={Register} />
            <Route exact path='/dashboard' component={Dashboard} />
            <Route exact path='/image/details/:id' component={Details} />
            <Route exact path='/images/user' component={MyImages} />
            <Route exact path='/images/view/:id' component={ImageView} />
            <Route exact path='/expositions' component={AllExpositions} />
            <Route exact path='/expositions/user' component={MyExpositions} />
            <Route exact path='/search/:name' component={Search} />
            <Route exact path='/exposition/details' component={ExpoDetails} />
            <Route exact path='/exposition/details/:id' component={ExpoDetails} />
            <Route exact path='/exposition/view/:id' component={ExpositionView} />
            <Route exact path='/exposition/edit/:id' component={ExpositionEdit} />
            <Route exact path='*' component={NotFound} />
        </Switch>
    </main>
);

export default Main;