import React from 'react'
import ReactDOM from 'react-dom'

import Header from '../Header'
import Gallery from '../Gallery'
import ExpoItem from '../Dashboard/ExpoItem.js'
import '../../styles/text.css'
import styles from '../../styles/home.module.css'
import createButton from '../../resources/createButton.png'
import galleryButton from '../../resources/GalleryButton.png'
import uploadButton from '../../resources/uploadButton.png'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'
import {withRouter} from 'react-router-dom'

class Home extends React.Component {
    constructor(props){
        super(props);

        this.state = {ids: [], expositions: []};
    }

    componentDidMount(){
        requester.get('/api/expositions/top')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    
                    console.log(res.expositions);
                    this.setState({
                        expositions: res.expositions
                    });
                }
                else
                    observer.fire('notifications', res);
            });
    }


    exposition3D = (url) => {
        this.props.history.push('exposition/view/' + url);
    }

    render(){

        return (
            <div>
                <Header transparent={false}></Header>
                <Gallery></Gallery>
                <div className={styles.content} ref='content'>
                    <div className={styles.item}>
                        <img src={createButton}></img>
                        <h1>Избери галерия</h1>
                        <span>Изберете подходящата атмосфера за вашета изложба от достъпните зали</span>
                    </div>
                    <div className={styles.item}>
                        <img src={galleryButton}></img>
                        <h1>Постави снимки</h1>
                        <span>Изберете подходящата атмосфера за вашета изложба от достъпните зали</span>
                    </div>
                    <div className={styles.item}>
                        <img src={uploadButton}></img>
                        <h1>Публикувай</h1>
                        <span>Публикувайте и споделете, направената от вас експозиция, с потенциални купувачи</span>
                    </div>
                    <div className={styles.item}>
                        <h1>Какво е VShow?</h1>
                        <span>
                            VShow е онлайн платформа за създаване на 3D експозиции на вашите картини, 3D модели, експонати 
                        </span>
                    </div>

                    <h1>Най-добрите експозиции</h1>
                    {
                        this.state.expositions.map((expo, i) => <ExpoItem src={expo ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + expo.mainImage:''} title='' key={i} click={()=>{this.exposition3D(expo.id)}}></ExpoItem>)
                    }
                </div>
            </div>
        )
    }
}
export default withRouter(Home);