import React from 'react'
import '../../styles/dashboard.css'

const ExpoItem2 = ({src, title, expo3D, isAdmin, isAuthor, addTop, deleteExpo}) => (
    <li className='expo-item2'>
        <img src={src} className='expo-img2' onClick={expo3D} alt='could not load'></img>
        <h2 className='title'>{title}</h2>
        {isAdmin ? <button className='expandbtn inline' onClick={addTop}><span>Добави към най-добрите</span></button>:''}
        <button className='expandbtn inline' onClick={expo3D}><span>Посети</span></button>
        {(isAdmin || isAuthor) ? <button className='expandbtn inline' onClick={deleteExpo}><span>Изтрий</span></button>:''}
    </li>
);

export default ExpoItem2;